<template lang="pug">
.atf(v-view)
  .top
    .upper-left(:class="{ threeDeeHover: threeDeeHover }")
      .title(:class="{ titleUp: titleIsUp }") {{ title }}
      .subtitle(:class="{ subTitleUp: subTitleIsUp }") {{ subtitle }}
      .subsubtitle(:class="{ subSubTitleUp: subSubTitleIsUp }") {{ subsubtitle }}
    .bganim(:class="{ threeDeeHover, threeDeeHover }")
  .bottom
    .threeDeeCont(
      :style="{ width: threeDeeWidthHeight, height: threeDeeWidthHeight }",
      :class="{ threeDeeHover: threeDeeHover }",
      @mouseover="(titleIsUp = true), (subTitleIsUp = true), (subSubTitleIsUp = true), (atfIsWaiting = true), (threeDeeHover = true)",
      @mouseleave="threeDeeHover = false",
      @touchstart="(titleIsUp = true), (subTitleIsUp = true), (subSubTitleIsUp = true), (atfIsWaiting = true), (threeDeeHover = true)",
      @touchend="threeDeeHover = false",
      @touchcancel="threeDeeHover = false"
    )
      svg.threeDeeE(
        :class="{ threeDeeRollin: isMounted }",
        :width="threeDeeWidthHeight",
        :height="threeDeeWidthHeight",
        ref="threeDeeE"
      )

    .intro(:class="{ animate: isMounted, shineIntro: threeDeeHover }")
      span.i1
        | {{ introOne }}&nbsp;
      span.i2
        | {{ introTwo }}

    .subintro(:class="{ animate: isMounted }") {{ subintro }}
</template>

<script>
import * as zdog from "zdog";
import VueScreenSize from "vue-screen-size";
export default {
  name: "AboveTheFold",
  props: {
    title: String,
    subtitle: String,
    subsubtitle: String,
    menutext: String,
    introOne: String,
    introTwo: String,
    subintro: String,
  },
  data() {
    return {
      isMounted: null,
      titleIsUp: false,
      subTitleIsUp: false,
      subSubTitleIsUp: false,
      threeDeeHover: false,
      upperRightHover: false,
      cphovered: false,
      twhovered: false,
      drhovered: false,
      ghhovered: false,
      bhhovered: false,
      rdhovered: false,
      mehovered: false,
      atfIsWaiting: false,
    };
  },
  mounted() {
    this.isMounted = true;

    // timeout function for atf is waiting
    setTimeout(() => {
      this.atfIsWaiting = true;
    }, 5000);

    // zdog stuff in guarantee function
    this.$nextTick(() => {
      console.log("mounted");
      // new zdog illustration
      this.illo = new zdog.Illustration({
        element: this.$refs.threeDeeE,
        dragRotate: true,
      });
      // group for E shapes
      this.erdGroup = new zdog.Group({
        addTo: this.illo,
        translate: { x: -60, y: 0, z: 30 },
      });
      // E shapes
      // 1
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 60,
        height: 230,
        // position further back
        translate: { x: 0, y: 0, z: 0 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 2
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: 0, z: 0 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 3
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: 115, z: 0 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 4
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: -115, z: 0 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 5
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 60,
        height: 290,
        // position further back
        translate: { x: -30, y: 0, z: -30 },
        rotate: { y: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 6
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 60,
        height: 290,
        // position further back
        translate: { x: 30, y: 0, z: -30 },
        rotate: { y: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 7
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 60,
        height: 290,
        // position further back
        translate: { x: 0, y: 0, z: -60 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 8
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: -145, z: -30 },
        rotate: { x: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 9
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: 145, z: -30 },
        rotate: { x: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 10
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: 84, z: -30 },
        rotate: { x: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 11
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: 30, z: -30 },
        rotate: { x: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 12
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: -30, z: -30 },
        rotate: { x: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 13
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: -84, z: -30 },
        rotate: { x: -zdog.TAU / 4 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 14
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: 0, z: -60 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 15
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: 115, z: -60 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // 16
      new zdog.Rect({
        addTo: this.erdGroup,
        width: 166,
        height: 60,
        // position further back
        translate: { x: 53, y: -115, z: -60 },
        stroke: 1,
        color: "#91E7A3",
        fill: true,
      });
      // draw first frame
      this.illo.updateRenderGraph();
      this.animate();
    });
  },
  methods: {
    animate() {
      // if (this.isSpinning) {
      this.illo.rotate.y += 0.0005;
      this.illo.rotate.z -= 0.00025;
      // }
      this.illo.updateRenderGraph();
      requestAnimationFrame(this.animate);
    },
  },
  computed: {
    // returns natural width/height (438)
    // unless screen is smaller than 500px,
    // then returns screen size minus 64
    threeDeeWidthHeight: function () {
      if (this.$vssWidth > 500) {
        return 438;
      } else {
        return this.$vssWidth;
      }
    },
  },
  // this.$vssWidth this.$vssHeight this.$vssEvent
  mixins: [VueScreenSize.VueScreenSizeMixin],
};
</script>

